.google-map-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
}

.google-map-wrapper .google-map {
    width: 100%;
    height: 100%;
}

.coming-soon-map .infoBox {
    font-size: 20px;
    font-weight: 600;
    line-height: 1.09;
    letter-spacing: -0.4px;
    text-align: center;
    color: #fff;
    width: 350px;
    background: $gold;
    padding: 15px 30px;
    margin-left: -150px;
    margin-top: -75px;
}

.coming-soon-map .infoBox img {
    display: none;
}

.coming-soon-map .infoBox::after {
    width: 0;
    height: 0;
    border-style: solid;
    margin-left: -12px;
    border-width: 12px 12px 0 12px;
    border-color: $gold transparent transparent transparent;
    position: absolute;
    top: 100%;
    left: 50%;
    display: block;
    content: ' ';
    z-index: 2;
}
